  computed: {
    // 映射icon对应名称
    mapIcon() {
      const obj = {};
      this.$nextTick(() => {
        this.mpkList.forEach((v) => {
          obj[v.icon] = v.description;
        });
      });
      return obj;
    },
  },
  methods: {
    // 添加icon
    AddIcon(i) {
      this.selectedIcon = this.selectMpk[i].icon || "";
      this.editXML = i;
      this.dialogVisible = true;
    },
    // 保存选择的icon
    saveMPK() {
      this.dialogVisible = false;
      this.selectMpk[this.editXML].icon = this.selectedIcon;
      this.selectMpk[this.editXML].description = this.$t(
        this.mapIcon[this.selectedIcon]
      );
    },
