<script>
import $ from "jquery";
import { Printd } from "printd";
import Qeditor from "@/components/Quill";
import IconSelect from "@/components/IconSelect";
import docCss from "./docCss";
import printCss from "./printCss";

// 函数防抖，防止用户多次点击按钮
function debounce(fn, t) {
  // 传一个回调函数
  let Timer = null;
  return function () {
    clearTimeout(Timer); // 清除定时器
    Timer = setTimeout(() => {
      fn.apply(this, arguments);
    }, t);
  };
}

export default {
  components: {
    Qeditor,
    IconSelect,
  },
  props: {
    load: {
      type: Boolean,
    },
  },
  data() {
    return {
      locale: "en",
      selectMpk: null, //被选中的icon列表
      custom: "lens", //定制类型
      editorHtml: "",
      isGHP61: true, //话机型号
      rn: "", //房间号
      table: {
        // new...
        project: "",
        enterprise: "",
        quantity: "",
        contact: "",
        phone: "",
        email: "",
        color: false,
        pms: "",
        specification: "",
        material: "",
        thick: "",
      },
      rns: [], //已被加载过的rn
      rn_: "", //上一次打印的房间号
      stick_type: "unicom", //贴纸模式样式类型
      paper: "A3",
      paperDialog: false,
      isFirefox: navigator.userAgent.indexOf("Firefox") > -1,
      isWin: navigator.userAgent.match(/Windows/i)
    };
  },
  created() {
    const locale = localStorage.getItem("locale");
    this.locale = locale || this.locale;
    if (this.custom === "lens") {
      this.editorHtml = localStorage.getItem("html_lens");
    } else {
      this.editorHtml = localStorage.getItem("html_stick");
    }
    const selectMpk = JSON.parse(localStorage.getItem("selectMpk"));
    const table = JSON.parse(localStorage.getItem("table"));
    this.selectMpk = selectMpk || this.selectMpk || this.defIconList;
    this.table = table || this.table;
  },
  computed: {
    defIconList() {
      return [
        { description: this.$t("FD-1"), icon: "FD-1" },
        { description: this.$t("RS-1"), icon: "RS-1" },
        { description: this.$t("EM-1"), icon: "EM-1" },
        { description: this.$t("WU-1"), icon: "WU-1" },
        { description: this.$t("RS-2"), icon: "RS-2" },
        { description: this.$t("HK-2"), icon: "HK-2" },
      ];
    },
    today() {
      let date = new Date();
      let yy = date.getFullYear();
      let mm = date.getMonth() + 1;
      let dd = date.getDate();
      return yy + "/" + mm + "/" + dd;
    },

    hasWhiteInEditorHtml() {
      if (this.isGHP61) return false;
      const WHITE_TXT = 'style="color: rgb(255, 255, 255);';
      return (this.editorHtml || "").includes(WHITE_TXT);
    },
    hasEmergency() {
      const hasEM1 = this.selectMpk.map((v) => v.icon).includes("EM-1");
      const RED_TXT = "color: rgb(209, 18, 13);";
      return this.isGHP61
        ? hasEM1
        : (this.editorHtml || "").includes(RED_TXT) || hasEM1;
    },
  },
  methods: {
    changeColor(v) {
      if (v) {
        this.table.pms = "Pantone Cool Gray 6 C";
      } else {
        this.table.pms = "";
      }
    },
    reset() {
      this.selectMpk = this.defIconList;
      this.table = {
        project: "",
        enterprise: "",
        quantity: "",
        contact: "",
        phone: "",
        email: "",
        color: false,
        pms: "",
        specification: "",
      };
      if (!this.isGHP61) {
        this.rn = "";
        this.$refs.editor.setHtml(1);
      }
      this.$message.closeAll();
      this.$message.success(this.$t("RESET_OK"));
    },
    saveHtml() {
      const editor = document.querySelector(".ql-container .ql-editor");
      if (this.custom === "lens") {
        localStorage.setItem("html_lens", editor.innerHTML);
      } else {
        localStorage.setItem("html_stick", editor.innerHTML);
      }
      localStorage.setItem("pages", this.rn);
      this.saveMpkList();
    },
    saveMpkList() {
      localStorage.setItem("selectMpk", JSON.stringify(this.selectMpk));
      localStorage.setItem("table", JSON.stringify(this.table));
      localStorage.setItem("locale", this.locale);
      this.$message.closeAll();
      this.$message.success(this.$t("SAVE_OK"));
    },
    setEditorHtml(h) {
      this.editorHtml = h;
    },

    // 获取房间值和个数,n为限制个数
    getRN(num) {
      const rn = this.rn.replace(/\s/, "");
      if (!/^(\d+[,，]|\d+-\d+[,，])*(\d+|\d+-\d+)+$/.test(rn)) {
        throw new Error("format");
      }
      if (this.rn === this.rn_) return this.rns;
      const nums = [];
      let stop = false; //是否达到停止个数
      rn.split(/[,，]/).forEach((n) => {
        if (/^\d+-\d+$/.test(n)) {
          const [start, end] = n.split("-");
          if (+end <= +start) throw new Error("format");
          for (let i = +start; i <= +end; i++) {
            nums.push((i + "").padStart(start.length, "0"));
            if (num && nums.length >= num) {
              stop = true;
              break;
            }
          }
          return false;
        }
        if (/^\d+$/.test(n)) {
          nums.push(n);
          if (stop || (num && nums.length >= num)) {
            throw new Error("extra 100");
          }
          return false;
        }
      });
      this.rns = nums;
      this.rn_ = this.rn;
      return nums;
    },

    // 生成文档页面
    getDoc() {
      if (!this.isGHP61 && $(".ghp2 #rn").length && this.custom === "lens") {
        const rnBox = $(".ghp2 #rn-box")[0];
        const pos = $(".ghp2 #rn")[0].classList.value.split("-")[2];
        $("#rn-line").remove();
        const line = document.createElement("span");
        line.id = "rn-line";
        const mark = document.createElement("span");
        mark.id = "rn-mark";
        mark.innerText = this.$t("TRANSPARENT_AREA");
        mark.style.cssText = `
            right: -220px;
            top: -15px;
            position: absolute;
            width: 200px;
            font-size: 24px;
      `;
        line.style.cssText =
          `
            left: 50%;
            top: 50%;
            position: absolute;
      ` +
          `
      ${
        pos == "l"
          ? "width: 374px;"
          : pos == "c"
          ? "width: 252px;"
          : "width: 134px;"
      }`;
        rnBox.style.position = "relative";
        rnBox.appendChild(line);
        line.appendChild(mark);
      }
      const dmain = document.querySelector(".d-main");
      dmain.innerHTML = "";
      dmain.innerHTML = this.$refs.print.outerHTML;
      return this.$refs.printDoc.outerHTML;
    },
    // 获取镜片模式的批量房间
    getLens() {
      if (!this.rn || !$(".ghp2 #rn-box").length) {
        return "";
      }
      const RNS = this.getRN(29106); //一百张限制

      // 按每页294个进行分割，
      const pages = RNS.reduce((pages, cur, i) => {
        const pageI = i % 294;
        if (pageI === 0) {
          pages.push(Array(294).fill(""));
        }
        const lastPage = pages[pages.length - 1];
        lastPage[pageI] = cur;
        return pages;
      }, []);
      return `
        <div id='numBox-lens'>
          ${pages
            .map((nums) => {
              return `
                <div class="page">
                  ${nums.map((n) => `<div class="box">${n}</div>`).join("")}
                </div>
              `;
            })
            .join("")}      
        </div>
      `;
    },
    getStick(v) {
      /* 1-->（无）rn控件+（有）房间号,批量只生成一页 */

      const stick = `<div class="wrap ">${this.$refs.print.outerHTML}</div>`;
      const limit =
        this.stick_type === "unicom"
          ? v === "A4"
            ? 9
            : 20
          : v === "A4"
          ? 12
          : 24;
      const note = `
        <div class="page-note">
            <span>Note: The dotted line is the cutting line</span>
            <span>${v}</span>
            <span>Scale:1:1</span>
        </div>
      `;

      if (this.rn && !$("#qeditor #rn").length) {
        let page = `<div class="paper">
        <div class="row2">
            <div class="row2-left"></div>
            <div class="page"><div class="page-box">
                ${stick.repeat(limit)}
                </div></div>
            <div class="row2-right">${note}</div>
        </div>
        <div class="row3">${note}</div>
                    </div>`;
        return `<div id="numBox-stick">${page}</div>`;
      }
      /* 先验证 [] 中格式（数字或空格，且必须存在数字），不满足当做普通文本处理 */
      let target = "";
      const span = document.querySelectorAll("#qeditor #rn span");

      if(!span.length) return ''

      span.forEach(s => {
          if(/.*\[$/.test(s.innerText.trim())){
              target+=s.innerText
          }
          if(/^\].*/.test(s.innerText.trim())){
            target+=s.innerText
          }
        if(s.style.backgroundColor==='rgb(255, 255, 255)'){
            target+=s.innerText
        }
      });

      if(!/.*\[[\s\d]*\d+[\s\d]*\]/.test(target))  return ""

      target = target.replace(/[^\d]/g, ""); //提取其中的数字进行替换

      /* 2-->（有）rn控件+（无）房间号,批量只生成一页+rn空白格 */
      if ($(".ghp2 #rn").length && !this.rn) {
        let page = `<div class="paper">
        <div class="row2">
          <div class="row2-left"></div>
          <div class="page"><div class="page-box">
               ${
                 stick +
                 stick
                   .replace(
                     eval("/(.*<span.*)" + target + "(.*span>.*)/g"),
                     "$1          $2"
                   )
                   .repeat(limit - 1)
               }
               </div></div>
          <div class="row2-right">${note}</div>
        </div>
        <div class="row3">${note}</div>
                    </div>`;
        return `<div id="numBox-stick">${page}</div>`;
      }


      /* 4-->（有）rn控件+（有）房间号,批量只生成一页 */
      let pages = [];
      let str = "";
      const RNS = this.getRN(380); //20张限制
      RNS.forEach((v, i) => {
        if (i && (i + 1) % limit === 0) {
          str += stick.replace(
            eval("/(.*<span.*)" + target + "(.*span>.*)/g"),
            "$1  " + v + "  $2"
          );
          let page = `<div class="paper">
            <div class="row2">
                <div class="row2-left"></div>
                <div class="page"><div class="page-box">${str}</div></div>
                <div class="row2-right">${note}</div>
            </div>
            <div class="row3">${note}</div>
                        </div>`;
          pages.push(page);
          str = "";
        } else {
          str += stick.replace(
            eval("/(.*<span.*)" + target + "(.*span>.*)/g"),
            "$1  " + v + "  $2"
          );
        }
      });
      //   不凑满一页的情况补全
      let add = 0;
      if (RNS.length >= 1 && RNS.length % limit) {
        add = (parseInt(RNS.length / limit) + 1) * limit - RNS.length;
        let page = `<div class="paper">
        <div class="row2">
            <div class="row2-left"></div>
            <div class="page"><div class="page-box">${(str += stick
              .replace(
                eval("/(.*<span.*)" + target + "(.*span>.*)/g"),
                "$1          $2"
              )
              .repeat(add))}</div></div>
            <div class="row2-right">${note}</div>
        </div>
        <div class="row3">${note}</div>
        </div>`;
        pages.push(page);
      }
      return `<div id="numBox-stick">${pages.join("")}</div>`;
    },

    validate() {
      const { quantity, contact, phone, email } = this.table;
      const errors = [];
      !quantity && errors.push("quantity");
      !contact && errors.push("contact");
      !phone && errors.push("phone");
      !email && errors.push("email");
      //   表格必填项验证
      if (errors.length) {
        // this.$emit("load", false);
        this.$message.closeAll();
        this.$message.error(this.$t("INPUT_REQUIRED"));
        return false;
      }
      return true;
    },

    paperOK() {
      this.paperDialog = false;
      this.getPdf();
    },

    getPdf() {
      const style = "./iconfonts/iconStyle.css";
      const style2 = "./iconfonts/quill.css";
      const scripts = []; //通过launchPrint延长时间，加载资源

      const section = document.createElement("section");
      let customRN = "";
      const { custom, isGHP61, paper } = this;
      if (!isGHP61 && custom === "lens") {
        try {
          customRN = this.getLens();
        } catch (err) {
          //   this.$emit("load", false);
          this.$message.closeAll();
          return this.$message.error(this.$t("ROOM_NUM_ERR"));
        }
        section.innerHTML = this.getDoc() + customRN;
        const printCallback = ({ launchPrint }) =>
          setTimeout(() => {
            launchPrint();
          }, 500);
        const d = new Printd();
        const iframe = d.getIFrame();
        const filename = isGHP61
          ? "GHP61x_silk_screen"
          : custom === "lens"
          ? "GHP62x_silk_screen"
          : "GHP62x_cardstock";

        iframe.addEventListener("load", () => {
          const { contentWindow } = iframe;
          contentWindow.document.title = filename; //实现Firefox打印导出的文件默认名=title
          document.title = filename; // other browers
        });

        d.print(
          section,
          [
            style,
            style2,
            printCss,
            docCss,
            `
                    @page {
                    size: A4 landscape;
                    margin: 5mm;
                    }`,
          ],
          scripts,
          printCallback
        );
      } else {
          try {
            customRN = isGHP61? '':this.getStick(paper);
          } catch (err) {
            if (err.message.includes("NO_NUM_ERR")) {
              return this.$confirm(
                this.$t("NO_NUM_ERR", {
                  0: `<img width="20" height="20" style="vertical-align: middle;" src="${require("../assets/center.svg")}" alt="" />`,
                }),
                this.$t("TIP"),
                {
                  center:true,
                  showClose: false,
                  showCancelButton: false,
                  dangerouslyUseHTMLString: true,
                  confirmButtonText: this.$t("SURE"),
                }
              );
            }
            //   this.$emit("load", false);
            this.$message.closeAll();
            return this.$message.error(this.$t("ROOM_NUM_ERR"));
          }

        section.innerHTML = this.getDoc() + customRN;
        const printCallback = ({ launchPrint }) =>
          setTimeout(() => {
            launchPrint();
          }, 500);
        const d = new Printd();
        const iframe = d.getIFrame();
        const filename = isGHP61
          ? "GHP61x_silk_screen"
          : custom === "lens"
          ? "GHP62x_silk_screen"
          : "GHP62x_cardstock";

        const { contentWindow } = iframe;
        iframe.addEventListener("load", () => {
          contentWindow.document.title = filename; //实现Firefox打印导出的文件默认名=title
          document.title = filename; // other browers
        });
        if (!isGHP61) {
          section.classList.add(paper);
          section.classList.add(this.stick_type);
        }

        d.print(
          section,
          [
            style,
            style2,
            printCss,
            docCss,
            `
                @page {
                size: ${
                  isGHP61 || paper === "A4"
                    ? "A4 landscape"
                    : `A3 ${
                        this.stick_type === "unicom" ? "portrait" : "landscape"
                      }`
                };
                margin:5mm;margin-bottom: 0mm ;
                ${
                  paper === "A3" && this.stick_type === "cutoff"
                    ? "margin-right: 0mm ;"
                    : isGHP61? "margin:5mm":''
                }
                }
                `,
          ],
          scripts,
          printCallback
        );
      }
    },

    // 打印pdf
    print: debounce(
      function () {
        if (!this.validate()) {
          return false;
        }

        this.paper = "A4";
        if (!this.isGHP61 && this.custom === "lens") {
          if (!this.color && !this.table.pms) {
            //   this.$emit("load", false);
            this.$message.closeAll();
            return this.$message.error(this.$t("LOGO_PMS"));
          }
          this.$confirm(
            this.$t("CONFIRM_LOGO_COLOR", { 0: this.table.pms }),
            this.$t("TIP"),
            {
              center: true,
              showClose: false,
              confirmButtonText: this.$t("CONFIRM"),
              cancelButtonText: this.$t("CLOSE"),
              dangerouslyUseHTMLString: true,
            }
          ).then(() => {
            this.paperDialog = true;
          });
        } else {
            if(this.custom==='stick'){
                this.paper='A3'
            }
          this.paperDialog = true;
        }
      },
      500,
      true
    ),
  },
};
</script>

<style lang="scss" scoped>
@import "./XML.scss";
